<script>
  import { Query } from '@sveltestack/svelte-query';
  import Skeleton from 'svelte-skeleton/Skeleton.svelte';
  import { SearchTerm, selection, fields } from './store';
  let page = 1;
  let s;

  $: s = $SearchTerm.toLowerCase();

  $: field = $fields;
  $: console.log(s.len);
  const url = `https://www.callboxinc.com/wp-json/cbtk/v1/case-studies`;
  async function fetchPosts({ page = 1, s, field }) {
    const res = await fetch(
      `${url}?s=${s}&page=${page}&per_page=10&fields=${field.join(',')}`
    );

    const data = await res.json();

    $selection = data[0].n;

    return data;
  }

  $: queryOptions = {
    queryKey: ['seeMore', page, s, field],
    queryFn: () => fetchPosts({ page, s, field }),
    enabled: $SearchTerm !== '',
    keepPreviousData: true,
    cacheTime: 1000 * 60 * 5,
  };
</script>

<Query options={queryOptions}>
  <div slot="query" let:queryResult>
    <div class="wrapper">
      {#if queryResult.isFetching}
        <Skeleton height="42" width="180" />
      {:else if queryResult.isError}
        <span>Error</span>
      {:else}
        <select class="rc-select" bind:value={$selection}>
          {#each queryResult.data as { n, label, id } (id)}
            <option value={n}>
              {label}
            </option>
          {/each}
        </select>
      {/if}
    </div>
  </div>
</Query>

<style>
  .wrapper {
    padding: 5px;
    position: relative;
  }
  select {
    -webkit-appearance: none;
    border-radius: 0px;
    background-image: url(data:image/png;base64,);
    background-position: center right;
    background-repeat: no-repeat;
    border-radius: 2px;
    outline: none;
    font: 1em 'HelveticaNeue', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: #181818;
    margin: 0;
    width: 100%;

    margin-bottom: 5px;
  }

  select.rc-select {
    width: 30%;
    padding: 7px;
    background-color: transparent;
    border: 1px solid rgba(95, 115, 128, 0.7);
    font-family: 'Lato', sans-serif;
    color: #292929;
  }
  select.rc-select {
    border: 1px solid rgba(95, 115, 128, 0.7);
    border-radius: 4px;
    color: #777;
    font-family: 'Lato', sans-serif;
    min-width: 200px;
    min-height: 43px;
    margin: 0;
    width: 100%;
  }
  .wrapper {
    padding: 0;
  }
</style>
